/* membership loader start */

.loder_Container {
  display: flex;
  height: 100%;
  align-items: center;
}

.loaderMembership {
  position: relative;
  margin-left: 50%;
  display: flex;
}

.dot_Loader {
  width: 0.7rem;
  height: 0.7rem;
  background-color: #fef3de;
  border-radius: 50%;
  position: absolute;
}

.dot1 {
  animation: gooeyEffect1 0.5s infinite alternate;
}

.dot2 {
  animation: gooeyEffect2 0.5s infinite alternate;
  left: 0.6rem;
}

@keyframes gooeyEffect1 {
  0% {
    transform: scale(1.2);
  }

  100% {
    transform: scale(0.8);
  }
}

@keyframes gooeyEffect2 {
  0% {
    transform: scale(0.8);
  }

  100% {
    transform: scale(1.2);
  }
}

/* membership loader ends */

