/* sidebar SOS button css start */

.circle {
  position: absolute;
  top: 90%;
  left: 50%;
  right: 50%;
  transform: translate(-50%, -50%);
  width: 6rem;
  height: 6rem;
  /* background: #fd2424; */
  /* color: white; */
  color: #fd2424;
  border: 1px solid #fd2424;
  text-align: center;
  line-height: 6rem;
  border-radius: 50%;
  font-size: 1.3rem;
  display: none;

  &:hover {
    cursor: pointer;
  }

  &::after,
  &::before {
    content: "";
    display: block;
    position: absolute;
    top: 0;
    left: 0;
    width: 6rem;
    height: 6rem;
    /* background: #fd242499; */

    border-radius: 50%;
    z-index: -1;
    /* animation: grow 1.3s ease-in-out infinite; */
  }

  &::after {
    /* background: #fd242480; */

    &::before {
      content: "";
      display: block;
      position: absolute;
      top: 0;
      left: 0;
      width: 6rem;
      height: 6rem;
      /* background: #fd242499; */
      border-radius: 50%;
      z-index: -1;
      /* animation: grow 1.3s ease-in-out infinite; */
    }
  }

  &::before {
    /* background: #fd242499; */
    animation-delay: -0.5s;
  }
}

@keyframes grow {
  0% {
    transform: scale(1, 1);
    opacity: 1;
  }

  100% {
    transform: scale(1.5, 1.5);
    opacity: 0;
  }
}

