/* --------------ElevatorInput css By Raj--------------------- */

.input-elevator {
  position: relative;
  z-index: 1;
  text-transform: capitalize;
}

.input-field-elevator {
  font-size: 18px;
  width: 100%;
  border: none;
  color: #8e8e8e;
  padding: 5px 0;
  background-color: transparent;
  outline: none;
}

.input-label-elevator {
  position: absolute;
  top: 0;
  left: 0;
  letter-spacing: 0.91px;
  color: #a7a7a7;
  transition: all 0.3s ease;
  pointer-events: none;
  font-size: 0.8rem;
}

.input-exist-elevator {
  position: absolute;
  bottom: -3px;
  left: 0;
  height: 1px;
  width: 100%;
  background-color: #ccc;
  transition: all 0.3s ease;
}

.input-highlight-elevator {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 2px;
  width: 0;
  background-color: #3301526c;
  transition: all 0.3s ease;
  z-index: 1;
}

.input-field-elevator:focus~.input-label-elevator {
  top: -20px;
  font-size: 1rem;
  font-weight: 500;
}

.input-field-elevator:focus~.input-label-elevator~.input-highlight-elevator {
  width: 100%;
}

.input-field:focus {
  cursor: default;
}

.input-field-elevator:hover+.input-label-elevator+.input-highlight-elevator {
  width: 100%;
  /* opacity: 0.7; */
}

