/*--------------------------Animated text Input css---------------*/


.input-field {
  font-size: 1.1rem;
  width: 100%;
  border: none;
  padding: 5px 0;
  background-color: transparent;
  outline: none;
  text-transform: initial;
  font-weight: 400;
  overflow: hidden;
  font-size: 1vw;
  font-family: "Montserrat", sans-serif;
  font-weight: 500;
  font-style: normal;
  letter-spacing: 0.91px;
  line-height: 1vw;
  color: #FFFFFF;
}

.text-field {
  padding-top: 8px;
}



.input-line {
  height: 1px;
  width: 100%;
  background-color: #cccccc77;
}

.input-highlight {
  position: absolute;
  bottom: 0;
  height: 1px;
  left: 0;
  width: 0;
  background-color: #FFFFFF;
  transition: all 0.3s ease;
  z-index: 1;
  opacity: 0.5;

}

.input-field:hover+.input-label,
.input-field:hover+.input-label+.input-highlight,
.input-field:hover+.input-label+.input-exist {
  opacity: 1;
}

.input-field:hover+.input-highlight {
  width: 100%;
}

.input-field:focus+.input-label {
  top: -20px;
  font-size: 14px;
  font-weight: 500;
  opacity: 1;
}

.input-field:focus+.input-label+.input-highlight {
  opacity: 1;
  width: 100%;
}

.input-field:focus+.input-label {
  opacity: 1;
}

.input-field:focus {
  cursor: none;
}

.input-field:hover+.input-label+.input-highlight {
  width: 100%;
  opacity: 0.7;
}

.input-field:hover+.input-label {
  opacity: 1;
}

