/* --------------------------------Animated Input CSS---------------------------------- */
input:-webkit-autofill {
  background-color: transparent !important;
  -webkit-background-color: transparent !important;
}

.input-container {
  position: relative;
  z-index: 1;
}

.select-container {
  position: relative;
  width: 25rem;
}

.input-field {
  font-size: 1.1rem;
  width: 100%;
  border: none;
  border-bottom: 1px solid #44444462;
  opacity: 1;
  padding: 5px 0;
  background-color: transparent;
  outline: none;
  font-size: 1rem;
  font-family: "Poppins";
  color: #1d1d1d;
  cursor: default;
  font-weight: 400;
}

.input-label {
  position: absolute;
  top: 0;
  left: 0;
  letter-spacing: 0.91px;
  color: #1d1d1d;
  transition: all 0.3s ease;
  pointer-events: none;
  font-size: 1rem;
  opacity: 0.5;
  font-weight: 400;
  z-index: 1;
}

.input-highlight {
  position: absolute;
  bottom: 0;
  left: 0;
  height: 1px;
  width: 0;
  background-color: #444444;
  transition: all 0.3s ease;
  opacity: 0.7;
}

.input-field:focus+.input-label {
  top: -20px;
  font-size: 14px;
  font-weight: 500;
  color: #1d1d1d;
  opacity: 1;
}

.input-field:focus+.input-label+.input-highlight {
  opacity: 1;
  width: 100%;
}

.input-field:focus+.input-label {
  opacity: 1;
}

.input-field:focus {
  cursor: none;
}

.input-field:hover+.input-label+.input-highlight {
  width: 100%;
  opacity: 0.7;
}

.input-field:hover+.input-label {
  opacity: 1;
}

