* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
  width: 100%;
}

.SingleEng {
  grid-area: SingleEng;
  position: relative;
  display: none;
  grid-area: SingleEng;
}

.SubSingleEng {
  /* margin-top: .4rem; */

  /* margin-bottom: 1.2rem; */
  width: 100%;
  height: 100%;
  display: grid;
  position: relative;
  grid-template-rows: 10% 5% 1% 84%;
  grid-row-gap: 0.2rem;
  padding: 0.5rem 1rem;
}

.container-attendance {
  display: flex;
  flex-direction: column;
  gap: 0.5rem;
}
.PDetails {
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: relative;
  height: 100%;
  width: 100%;
}

.Pimg {
  height: 4rem;
  width: 4rem;
  display: flex;
  position: relative;
  background-color: transparent;
  border: 1px solid #707070;
  border-radius: 5px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
}

.Pimg img {
  height: 100%;
  width: 100%;
}

.ODetailsColumn {
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
}

.ODetails {
  position: relative;
  height: 100%;
  width: 100%;
  position: relative;
}

.EngeeniersCard {
  min-height: 87vh;
  width: 100%;
  background-color: #ffffff;
  border-radius: 20px;
  display: grid;
  grid-template-rows: 1fr;
  grid-template-areas: "EngeeniersSubCard";
}

.EngeeniersSubCard {
  grid-area: EngeeniersSubCard;
}

.AllCards {
  max-height: 83vh;
  overflow-y: auto;
  margin-bottom: 1.2rem;
  margin-top: 1.2rem;
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  grid-row-gap: 4rem;
  grid-column-gap: 7rem;
  padding: 2rem 4rem;
}

.EngeeniersChatF {
  border-radius: 1.2rem;
  position: relative;
  height: 95%;
  margin-bottom: 1.2rem;
  display: none;
}

.AllCards::-webkit-scrollbar-track {
  border-radius: 10px;
}

.AllCards::-webkit-scrollbar {
  width: 5px;
}

.AllCards::-webkit-scrollbar-thumb {
  border-radius: 10px;
  background-color: #ffffff;
}

.AllCards:hover::-webkit-scrollbar-thumb {
  background-color: #f8ac1d;
}

.EngCards {
  background-color: #ffffff;
  border-radius: 8px;
  box-shadow: 2px 4px 10px 1px #00000029;
  min-height: 20vh;
  position: relative;
  display: flex;
  align-items: center;
  /* justify-content: space-between; */
  gap: 2.2rem;
  flex-direction: column;
  padding: 0.4rem;
}

.EngCardCheckinIndicator {
  position: absolute;
  width: 10px;
  height: 10px;
  background-color: #fac663;
  border-radius: 50%;
  right: 5%;
  top: 10%;
}

.EngCardDetails {
  display: flex;
  width: 90%;
  justify-content: space-between;
  position: relative;
  height: 50%;
}

.EngCardDetailsL {
  display: flex;
  margin-top: 1rem;
  /* width: 30%; */
  position: relative;
  background-color: transparent;
  border: 1px solid #707070;
  border-radius: 10px;
  object-fit: cover;
  object-position: center;
  overflow: hidden;
}

.EngCardDetailsL img {
  height: 100px;
  width: 100px;
  border-radius: 10px;
}

.EngCardRText {
  display: flex;
  width: 10vw;
  justify-content: center;
  align-items: center;
}

.EngCardDetailsR {
  width: 70%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
}

.table-container {
  margin-top: 1.8rem;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 0.1rem;
  font-family: "Noto Sans KR", sans-serif;
  width: 60%;
  position: relative;
  text-align: start;
  height: auto;
  word-wrap: break-word;
  overflow-wrap: break-word;
  position: relative;
}

.table-item {
  font-weight: 600;
  color: #343434;
  border-bottom: 2px solid transparent;
  font-size: 0.9rem;
  word-wrap: break-word;
  overflow-wrap: break-word;
}

.table-container > .table-item:nth-child(even) {
  font-size: 0.8rem;
  color: #444444;
  font-weight: 500;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.table-container > .table-item:nth-child(odd) {
  font-size: 0.8rem;
  color: #444444;
  font-weight: 400;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.EngCardCash {
  height: 4vh;
  width: 90%; /*Paras*/
  display: flex;
  justify-content: space-evenly;

  align-items: center;
  background-color: #fff9ef;
  box-shadow: inset 1px 2px 3px 1px #00000029;
  border-radius: 5px;
  margin-bottom: -7rem;
}

.EngCardCash h5 {
  text-wrap: nowrap;
  display: flex;
  gap: 0.5rem;
  text-align: start;
  font-size: 0.8rem;
  color: #444444;
  font-weight: 400;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.EngCardCash h5 span {
  font-size: 0.8rem;
  color: #444444;
  font-weight: 500;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.HoriZontalLine {
  height: 60%;
  border-radius: 1px;
  width: 1px;
  background-color: #7070704d;
}

.AHoriZontalLine {
  height: 80%;
}

.vertical-line {
  height: 1px;
  width: 100%;
  background-color: #70707018;
  position: relative;
}

.overlay-vertical-line {
  height: 1px;
  width: 25%;
  background-color: #f8ac1d;
  position: relative;
  position: absolute;
  transition: 0.5s ease;
}

.EngCardMessage {
  height: 5vh;
  width: 100%;
  margin-top: 1rem;
  bottom: -20%;
  position: relative;
}

.EngChatNav {
  height: 40px;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  display: none;
}

.VerticalLine {
  height: 2%;
  width: 25%;
  background-color: #7070704d;
}

.EngChatNavText {
  width: 80%;
  height: 100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.EngChatNavText h5 {
  text-wrap: nowrap;
  font-size: 1.1rem;
  font-size: 0.8rem;
  color: #444444;
  font-weight: 500;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.EngChatBoxHead {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 0.8rem;
  border-bottom: 1px solid #00000029;
}

.EngChatBoxIcons {
  width: 50%;
  display: flex;
  align-items: center;
  justify-content: flex-end;
  gap: 0.4rem;
}

.EngChatBox h6 {
  text-wrap: nowrap;
  font-size: 1.1rem;
  font-size: 0.8rem;
  color: #444444;
  font-weight: 500;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.EngChatBox {
  width: 100%;
  background-color: #fffbf4;
  border-radius: 5px;
  box-shadow: 1px 2px 5px 1px #00000029;
  position: relative;
  /* height: 100%; */
  margin-top: 1rem;
}

.EngChatMsg {
  /* height: 85%; */
  width: 100%;
  position: relative;
}

.SubEngChatMsg {
  /* height: 100%; */

  height: 68vh;
  width: 100%;
  padding: 0rem 1rem;
  padding-bottom: 2rem;
  overflow-y: auto;
}

.engchatmsg-sender-side {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  margin-top: 0.4rem;
}

.engchatmsg-reciver-side {
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-top: 0.4rem;
}

.engchatmsg-reciver-message,
.engchatmsg-sender-message {
  max-width: 70%;
  border-radius: 5px;
  padding: 0.5rem 0.5rem;
  font-size: 0.9rem;
  text-wrap: wrap;
  line-height: 1.1;
  text-align: start;
  font-family: "Poppins";
}

.engchatmsg-sender-message {
  color: #444444;

  /* background: #fef3de; */
  margin-left: 8.4rem;
  margin-top: 0.2rem;
  word-wrap: break-word;
  background-color: #fef3de;
  font-family: "Poppins";
  box-shadow: 1px 2px 5px #00000029;
  opacity: 1;
}

.engchatmsg-reciver-side {
  /* padding: 10px 6px; */
  margin-top: 0.5rem;
  font-family: "Poppins";
  margin-right: 0.3rem;
  word-wrap: break-word;
}

.engchatmsg-reciver-message {
  color: #000;
  background: #ffffff 0% 0% no-repeat padding-box;
  box-shadow: 1px 2px 5px #00000029;
  opacity: 1;
  margin-top: 0.5rem;
  font-family: "Poppins";
  margin-left: 0.3rem;
  word-wrap: break-word;
}

.eng-icon {
  font-size: 1rem;
}

.PDetails h1 {
  font-size: 0.8rem;
  color: #444444;
  font-weight: 400;
  font-family: "Poppins";
  line-height: 1.3rem;
  display: flex;
  gap: 0.5rem;
}

.PDetails h1 span {
  font-size: 0.8rem;
  color: #444444;
  font-weight: 490;
  font-family: "Poppins";
  line-height: 1.3rem;
}

.SubPDetails {
  display: flex;
  width: 27%;
  height: 100%;
  justify-content: space-around;
  align-items: center;
}

.Icon_Color {
  color: #f8ac1d;
  cursor: pointer;
}

.ODetailsColumn h5 {
  text-align: left;
  font-family: "Poppins";
  font-weight: 500;
  margin-bottom: 0.3rem;
  letter-spacing: 0px;
  color: #444444;
  font-size: 0.9rem;
  cursor: pointer;
}

.TaskHistory {
  min-height: 100%;
  width: 100%;
  display: grid;
  grid-template-rows: 7%;
}

.TaskHeading {
  position: relative;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  /* border-bottom: 2px solid #5353533b; */
}

.TaskHeading h5 {
  color: #1d1d1d;
  font-size: 1rem;
  font-weight: 550;
  margin-bottom: 0.3rem;
}

.AllTask {
  min-height: 100%;
  width: 100%;
  position: relative;
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-column-gap: 0.3rem;
  overflow-y: auto;
}

.Services {
  margin-top: 1rem;
  padding: 0.9rem;
  width: 100%;
  height: 100%;
  position: relative;
}

.CallBacks {
  margin-top: 1rem;
  padding: 0.9rem;
  width: 100%;
  height: 100%;
  position: relative;
}

.ServicContainer {
  height: 65vh;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}

.CallbackContainer {
  height: 65vh;
  margin-top: 1rem;
  display: flex;
  flex-direction: column;
}

.AllServices,
.AllCallback {
  height: 100%;
  width: 100%;
  display: grid;
  grid-template-columns: 50% 50%;
  grid-gap: 1rem;
  padding: 1rem;
}

.ServiceCards,
.CallBackCards {
  border-radius: 10px;
  /* height: 10vh; */
  background-color: #fffbf4;
  box-shadow: 1px 2px 5px #00000029;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  gap: 0.5rem;
  padding: 0.2rem;
  height: 10vh;
  padding: 1rem;
}

