.CallBackCardsBottom {
  width: 100%;
  height: 20%;
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-around;
}

.CallBackCardsBottom > h5:nth-child(1) {
  font-family: " Poppins";
  font-size: 0.9rem;
  font-weight: 700;
